export default {
  data () {
    return {
      actionShow: true
    }
  },
  methods: {
    Add () {
      this.$emit('Add')
    },
    Delete () {
      this.$emit('Delete')
    },
    preDelete () {
      this.actionShow = false
      this.$emit('preDelete')
    },
    backPre () {
      this.actionShow = true
      this.$emit('backPre')
    },
    deleteSuccess (code) {
      if (code === 200) {
        this.actionShow = true
      } else {
        this.actionShow = false
      }
    }
  }
}



